import React, { Component } from 'react'
import { Link } from 'react-router'
import axios from 'axios'
import styles from './Kabupaten.css'

class Kabupaten extends Component {

  constructor(props, context) {
    super(props, context)
    this.state = {
      type: '',
      periode: '',
      priceMin: '',
      priceMax: '',
      kriteria: '',
      list: [],
      loading: 'none',
      label: '',
      page: 1,
      total: 0
    };
  }

  loadData(slug) {
    this.setState({
      loading: 'block'
    });
    axios.get('http://botek.ngekos.co.id/api/v1/kota/'+slug+'.json?type='+this.state.type+'&periode='+this.state.periode+'&priceMin='+this.state.priceMin+'&priceMax='+this.state.priceMax+'&kriteria='+this.state.kriteria+'&page=')
    .then((data) => {
      this.setState({
        list: data.data.data.data,
        label: data.data.detail.label,
        total: data.data.detail.total,
        loading: 'none',
      });
    });
  }

  setHunian(event) {
    this.setState({
      type: event.target.value
    }, () => this.loadData(this.props.params.slug));
  }

  setPeriode(event) {
    this.setState({
      periode: event.target.value
    }, () => this.loadData(this.props.params.slug));
  }

  setKriteria(event) {
    this.setState({
      kriteria: event.target.value
    }, () => this.loadData(this.props.params.slug));
  }

  componentDidMount() {
    this.loadData(this.props.params.slug);
  }

  render() {
    var main = this.state.list.map((item) => {
        return (
            <div className="ngekos-hotel-post-wrapper col-sm-6 col-md-4" key={item.id}>
                <div className="ngekos-hotel-post">
                    <Link to={`/hunian/${item.slug}`} className="ngekos-hotel-thumbnail">
                        <img src={`http://botek.ngekos.co.id/upload/ngekos/thumbs/300_${item.sampul}`} alt={item.nama}/>
                    </Link>
                    <div className="ngekos-hotel-description">
                        <div className="ngekos-rating-stars">
                            <p className="ngekos-hotel-reviews">
                                <i className="fa fa-male"></i> Putra
                            </p>
                        </div>
                        <div className="ngekos-description-content">
                            <h2 className="ngekos-entry-title">
                              <Link to={`/hunian/${item.slug}`}>{item.nama}</Link>
                            </h2>
                            <h5 className="ngekos-hotel-location">
                                <Link to={`/hunian/${item.slug}`}>
                                  <i className="fa fa-map-marker"></i>{item.alamat}
                                </Link>
                            </h5>
                        </div>
                        <div className="ngekos-hotel-price">
                            <span className="ngekos-cost">Rp {new Intl.NumberFormat().format(item.tarif_perbulan)}</span><br/>
                            <span className="ngekos-per">per bulan</span>
                        </div>
                    </div>
                </div>
            </div>
        );
    });

    return (
      <div className="ngekos-pencarian-padding">
        <p className="breadcrumb hidden-xs">
          Pencarian &gt; Kabupaten/Kota &gt; {this.state.label} ({this.state.total} Hunian Ditemukan)
          <a href="#" id="pencarian" className="pull-right">Pencarian Lain</a>
        </p>
        <p className="breadcrumb visible-xs">
          Pencarian &gt; {this.state.label} ({this.state.total} Hunian)
        </p>
        <div className="row">
          <aside className="col-sm-4 col-md-3 col-lg-3 ngekos-sidebar hidden-xs">
              <div className="ngekos-sidebar-detail">
                  <div className="ngekos-sidebar-menu">
                      <div className="ngekos-sidebar-tab">
                          Tipe Hunian <i className="caret pull-right"></i>
                      </div>
                      <div className="ngekos-sidebar-konten">
                          <p className="ngekos-input-label"></p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="type" id="kost" value="kost" checked={this.state.type=='kost'} onChange={this.setHunian.bind(this)}/>
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="kost">Kost</label>
                          </p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="type" id="bangsalan" value="bangsalan" checked={this.state.type=='bangsalan'} onChange={this.setHunian.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="bangsalan">Bangsalan</label>
                          </p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="type" id="kontrakan" value="kontrakan" checked={this.state.type=='kontrakan'} onChange={this.setHunian.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="kontrakan">Kontrakan</label>
                          </p>
                      </div>
                  </div>

                  <div className="ngekos-sidebar-menu">
                      <div className="ngekos-sidebar-tab" >
                          Periode Sewa <i className="caret pull-right"></i>
                      </div>
                      <div className="ngekos-sidebar-konten">
                          <p className="ngekos-input-label"></p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="periode" id="harian" value="harian" checked={this.state.periode=='harian'} onChange={this.setPeriode.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="harian">Harian</label>
                          </p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="periode" id="mingguan" value="mingguan" checked={this.state.periode=='mingguan'} onChange={this.setPeriode.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="mingguan">Mingguan</label>
                          </p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="periode" id="bulanan" value="bulanan" checked={this.state.periode=='bulanan'} onChange={this.setPeriode.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="bulanan">Bulanan</label>
                          </p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="periode" id="tahunan" value="tahunan" checked={this.state.periode=='tahunan'} onChange={this.setPeriode.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="tahunan">Tahunan</label>
                          </p>
                      </div>
                  </div>

                  <div className="ngekos-sidebar-menu">
                      <div className="ngekos-sidebar-tab">
                          Kriteria Penghuni <i className="caret pull-right"></i>
                      </div>
                      <div className="ngekos-sidebar-konten">
                          <p className="ngekos-input-label"></p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="kriteria" id="pria" value="pria" checked={this.state.kriteria=='pria'} onChange={this.setKriteria.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="pria">Khusus Pria</label>
                          </p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="kriteria" id="wanita" value="wanita" checked={this.state.kriteria=='wanita'} onChange={this.setKriteria.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="wanita">Khusus Wanita</label>
                          </p>
                          <p className="ngekos-radio-line">
                              <label className="switch-label-check">
                                <input type="radio" ref="kriteria" id="wanita-pria" value="wanita-pria" checked={this.state.kriteria=='wanita-pria'} onChange={this.setKriteria.bind(this)} />
                                <span className="switch"></span>
                              </label>
                              <label htmlFor="wanita-pria">Wanita Pria</label>
                          </p>
                      </div>
                  </div>

              </div>
          </aside>

          <main className="col-xs-12 col-sm-8 col-md-9 col-lg-9 ngekos-search-result ngekos-grid-view" role="main">
              <div className="row">
                <div className="ngekos-loader-overlay" style={{display:this.state.loading}}></div>
                {main}
              </div>
          </main>
        </div>
      </div>
    )
  }
}

module.exports = Kabupaten



